export async function getProfessionals({ category }) {
  // Example mock data
  const mockData = {
    items: [
      {
        id: 1,
        name: "AC Repair Service",
        imageUrl: "/ac-repair.jpg"
      },
      {
        id: 2,
        name: "Plumbing Expert",
        imageUrl: "/plumber.png"
      },
      {
        id: 3,
        name: "Electrician Service",
        imageUrl: "/Electrician service.png"
      }
    ]
  }

  // Simulate filtering by category
  if (category !== "All Services") {
    return {
      items: mockData.items.filter(item =>
        item.name.toLowerCase().includes(category.toLowerCase())
      )
    }
  }

  return mockData
}
